<template>
  <div class="correct">
    <my-modal-box ref= "msg">
        <AddRep />
    </my-modal-box>
    <Header />
    <div class="correctTable">
      <div class="correctTitle">
        <div>
          <span style="margin: 0 15px; font-size: 26px">|</span>
          <span>报告列表</span>
        </div>
        <div class = 'correctBackBtn' @click="correctBackBtn()">
          <span >返回</span>
        </div>
      </div>
      <div class="correctBtn">
        <el-button @click="handleAddRep()"
          >新增报告<i class="el-icon-upload el-icon--right"></i
        ></el-button>
      </div>
      <div class="correctTabMain">
        <el-table
          :data="
            correctList.slice(
              (currentPage - 1) * pageSize,
              currentPage * pageSize
            )
          "
          stripe
          style="width: 100%"
        >
          <el-table-column
            prop="date"
            label="序号"
            min-width="8%"
            type="index"
            align="center"
          >
          </el-table-column>
          <!-- <el-table-column prop="date" label="序号" width="100">
          </el-table-column> -->
          <el-table-column prop="company_name" label="公司名称" min-width="50%">
          </el-table-column>
          <el-table-column prop="create_name" label="创建者" min-width="10%">
          </el-table-column>
          <el-table-column  label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                @click="reportDelete(scope.row.id)"
                type="text"
                size="small"
                >删除</el-button
              >
              <el-button type="text" size="small" @click="lookCurrent(scope.row)">查看</el-button>
              <el-button type="text" size="small" @click="ReportDownLoad(scope.row.id)">下载</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="correctPage">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[4, 6, 8]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="correctList.length"
        >
        </el-pagination>
      </div>
    </div>
    <div class = 'footerContainer'>
      <Footer />
    </div>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex'
import { SERVE_ADDRESS } from '@/config/utils/constants'
// import { getCookie } from '@/config/utils'
export default {
  name: "correct",
  components: {
    Header: () => import("@/components/HeaderComponent.vue"),
    Footer: () => import("@/components/FooterComponent.vue"),
    AddRep: () => import("@/components/correctModify/CorrectAddRep.vue"),
  },
  data() {
    return {
      currentPage: 1,
      pageSize: 6,
    };
  },
  computed:{
    ...mapState({
      correctList: state => state.correct.correctList
    })
  },
  methods: {
    ...mapActions({
      getCottectList: 'correct/getCottectList',
      reportDelete: 'correct/reportDelete',
    }),
    ...mapMutations({
      saveCurrentReport: 'correct/saveCurrentReport',
    }),
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage = val;
    },
    handleAddRep(){
        console.log(this.$refs)
        this.$nextTick(() => this.$refs.msg.msgPlugin(true))
    },
    lookCurrent( report ){
        this.saveCurrentReport(report)
        this.$handleGo('report')
    },
    correctBackBtn(){
      this.$router.back()
    },
    // handleClick(data){
    //   console.log(data)
    //   this.reportDelete(data.id)
    // }
    ReportDownLoad(id){
      window.open(`${SERVE_ADDRESS}/downloadreport/${id}`);
    }
  },
  created(){
    this.getCottectList()
  },
  mounted(){
    // console.log(this.$store.dispatch('correct/getCottectList'))
    // this.$store
    // console.log(this.getCottectList().then(() => console.log(this.$store.state)))
    // console.log(getCookie('csrftoken'))
  }
};

</script>

<style>
.correct {
  width: 100%;
  height: 100%;
  background: #efefef;
}
.correctTable{
  min-height: calc(100% - 130px - 60px);
}
.correctTitle {
  line-height: 50px;
  background: rgba(0, 0, 96, 1);
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  text-align: left;
  padding-left: 60px;
  display: flex;
  justify-content:space-between;
}
.correctBackBtn{
  cursor: pointer;
  margin-right:50px;
}
.correctBtn {
  margin: 15px;
  width: 300px;
}
.correctBtn .el-button{
    width: 120px;
    background: rgba(0,0,96,1);
    color:#fff;
    margin:0 10px;
}
.correctBtn .el-button:hover{
    width: 120px;
    background: rgba(0,0,96,.8);
    color:#fff;
}
.correctTabMain {
  width: 90%;
  /* height: 80%; */
  /* height: 500px; */
  margin: 0 auto;
  overflow: hidden;
}
.correctPage {
  padding: 40px;
}
.el-pagination__editor.el-input{
    vertical-align: bottom;
}
.footerContainer{
  height: 60px;
  position: relative;
}
</style>